.center() {
  align-items: center;
  justify-content: center;
}

.seasoning-img {
  .seasoning-img-children {
    display: inline-block;
  }

  &.seasoning-img-tips {
    display: flex;
    .center();
    font-size: 12px;
  }

  &.seasoning-img-mode {
    display: flex;
    overflow: hidden;
  }

  /**
   *  image 
   */
  &.seasoning-img-mode-scaleToFill {
    .seasoning-img-children {
      width: 100%;
      height: 100%;
    }
  }

  /**
   * 
   */
  &.seasoning-img-mode-aspectFit {
    .center();
  }

  /**
   * 
   */
  &.seasoning-img-mode-aspectFill {
    .center();
  }

  /**
   * 
   */
  &.seasoning-img-mode-center {
    .center();
  }

  /**
   * 
   */
  &.seasoning-img-mode-left {
    align-items: center;
    justify-content: flex-start;
  }

  /**
   * 
   */
  &.seasoning-img-mode-right {
    align-items: center;
    justify-content: flex-end;
  }

  /**
   * 
   */
  &.seasoning-img-mode-top {
    align-items: flex-start;
    justify-content: center;

    /**
     * 
     */
    &.seasoning-img-mode-left {
      justify-content: flex-start;
    }

    /**
     * 
     */
    &.seasoning-img-mode-right {
      justify-content: flex-end;
    }
  }

  /**
     * 
     */
  &.seasoning-img-mode-bottom {
    align-items: flex-end;
    justify-content: center;

    /**
     * 
     */
    &.seasoning-img-mode-left {
      justify-content: flex-start;
    }

    /**
     * 
     */
    &.seasoning-img-mode-right {
      justify-content: flex-end;
    }
  }
}

@s-p: #1890ff;@s-s: #52c41a;@s-w: #faad14;@s-e: #f5222d;@s-fs: 16px;@s-c: rgba(0, 0, 0, 0.85);@s-cs: rgba(0, 0, 0, .45);@s-dc: rgba(0, 0, 0, .25);@s-dcb: rgba(0, 0, 0, .05);@s-bc: #eee;@s-bcd: #ccc;@s-b: #fff;